body{
   background-color: #123456;
   display: grid;
   align-items: center;
   justify-items: center;
}
.App{ 
  display: grid;
align-items: center;
justify-items: center;
width: auto;
height: auto;
}
.bar{
  display: grid;
  align-items: center;
  justify-items: center; 
 grid-template-columns: 1fr 80px 1fr;
  width: 450px;
  height: 80px;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 24pt;  
  -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
}
.board{   
  background-color: black;
  display: grid;
  align-items: center;
  justify-items: center;  
  grid-template-columns: 150px 150px  150px;
  grid-template-rows: 150px 150px  150px;
 grid-gap: 5px;
}
.userImg{
  width: auto;
  height:auto;
}
.items{
  background-color:#123456;
  display: grid;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 100%;
  font-size: 48pt;
}

